var store = require('../store')

module.exports = {
  props: ['params'],
  data: function() {
    return {
      topic: {}
    }
  },
  watch: {
    'params.topicId': 'update'
  },
  compiled: function() {
    this.update()
  },
  methods: {
    update: function() {
      var id = this.params.topicId
      if(!id) return

      this.fetchTopic(id)
    },
    fetchTopic: function(id) {
      this.topic = {}

      store.fetchTopicById(id)
        .then(function(result) {
          this.topic = result.data
        }.bind(this))
    }
  }
}


